.carrossel_principal {
    margin-top: 24px;
    position: relative;

    &--item {
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;

        outline: none;
        width: 100%;
        height: 190px;

        @include media-min992 {
            height: 377px;
        }
    }

    .slick-dots {
        display: flex;
        align-self: center;
        list-style: none;
        position: absolute;
        margin: 0;
        bottom: -16px;
        left: 46%;
        transform: translate(-50%);

        @include media-min992 {
            left: 48%;
            bottom: -26px;
        }

        li {
            width: 5px;
            height: 5px;
            border-radius: 50%;
            background-color: #B5B5B5;
            transition: all .4s ease;

            &:hover {
                background-color: #FF8D0A;
            }

            &.slick-active {
                width: 10px;
                height: 5px;
                border-radius: 20px;
                background-color: #FF8D0A;
            }

            button {
                background-color: transparent;
                border: none;
                text-indent: -9999px;
                outline: none;
            }
        }
        
        li + li {
            margin-left: 4px;
        }
    }

    .slick-arrow{
        position: absolute;
        top: 50%;
        z-index: 10;
        color: white;
        transform: translateY(-50%);

        &:focus{
            box-shadow: none;
        }

        &.btn-prev{
            left: 25px;
        }

        &.btn-right{
            right: 25px;
        }
    }
}