.fornecedores {

    .sect-titulo {
        margin-bottom: 20px;
    }

    &--lista {
        margin: 0;
        display: flex;
        flex-wrap: wrap;

        li {
            margin-bottom: 25px;
            width: 50%;
            display: flex;
            align-items: center;
            font: normal 16px/18px Arial, sans-serif;
            color: #353535;

            @include media-min768 { width: 33%; }
            @include media-min992 { width: 25%; }

            &::before {
                margin-right: 6px;
                content: '';
                display: block;
                width: 8px;
                height: 8px;
                background-color: #353535;
                border-radius: 50%;
                transition: all 0.4s ease;
            }

            &:hover,
            &.active {
                a {
                    color: $cor-laranja;
                    text-decoration: underline;
                }

                &::before {
                    background-color: $cor-laranja;
                }
            }
        }
    }
}