.tag {
    padding: 10px 12px;
    margin-right: 5px;
    margin-bottom: 5px;
    height: 37px;
    border-radius: 6px;
    font: normal 14px/16px Arial, sans-serif;
    
    i,
    img,
    span {
        margin-left: 10px;
        cursor: pointer;
    }

    &--roxo {
        background-color: #008ed1;
        color: #FFFFFF;
    }

    &--card-item {
        padding: 8px;
        position: absolute;
        top: 0;
        right: 0;
        background-color: #FF8D0A;
        border-radius: 6px;
        color: white;
        font: bold 14px/16px Arial, sans-serif;
        text-transform: uppercase;
    }

    &--table {
        position: absolute;
        top: -12px;
        right: -12px;
        width: 16px;
        height: 17px;
        font: bold 12px/14px Arial, sans-serif;
        border-radius: 6px;
        color: white;
        display: flex;
        align-items: center;
        justify-content: center;

        &-laranja {
            background-color: $cor-laranja;
        }

        &-roxo {
            background-color: $cor-roxo;
        }
    }
}