.titulo {
    color: #1E1E1E;
    font-weight: 700;

    &-formulario {
        font-size: 18px;
        font-weight: normal;
        line-height: 21px;
    }

    &--bold {
        font-weight: 700;
    }
}

.h2 {
    font-size: 24px;
    line-height: 28px;
}

.h3 {
    font-size: 22px;
    line-height: 26px;
}

.h4 {
    font-size: 20px;
    line-height: 23px;
}

.h5 {
    font-size: 16px;
    line-height: 18px;
}

.h6 {
    font-size: 14px;
    line-height: 16px;
}