/* ==========================================================================
   Geral
   ========================================================================== */

body {
  font-family: Arial, sans-serif;
  color: #666666;
  background-color: white;

  &.menu-fixed{
    padding-top: 168px;
  }
}

.overlay{
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0, 0.7);
  opacity: 0;
  transition: opacity ease 0.6s;

  &.active{
    opacity: 1;
  }
}

.tooltip{

  .arrow:before{
    border-top-color: #008ed1;
  }

  .tooltip-inner{
    padding: 12px 24px;
    background-color: #008ed1;
    font-size: 14px;
    border-radius: 10px;
  }

}

.dropdown-menu{
  min-width: 240px;
  max-width: 325px;
  padding: 0;
  color: #636363;
  border-radius: 0;
  border: 1px solid #F4F4F4;
  box-shadow: 2px 4px 6px 0 rgba(0,0,0, 0.12);

  &_cont{
    padding: 25px;

    li + li {
      margin-top: 12px;
    }
  }

  &_footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px 25px;
    background-color: #F2F2F2;

    .total{
      color: #525252;
      font-size: 12px;

      p{
        margin-bottom: 0;
      }

      b{
        display: block;
        margin-top: 6px;
        font-size: 18px;
        color: $cor-laranja;
      }
    }
  }

  &::before{
    content: '';
    position: absolute;
    top: -13px;
    right: 30px;
    z-index: 2;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 13px 13px 13px;
    border-color: transparent transparent white transparent;
  }

  &::after{
    content: '';
    position: absolute;
    top: -14px;
    right: 29px;
    z-index: 1;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 14px 14px 14px;
    border-color: transparent transparent #F4F4F4 transparent;
  }

  &_link{
    span{
      display: inline-block;
      vertical-align: middle;
    }

    &:hover{
      color: $cor-laranja;
    }
  }
}

.badge{
  vertical-align: middle;
  margin-left: 8px;
  min-width: 20px;
  border-radius: 6px;

  &-roxo{
    background: $cor-roxo;
    color: white;
  }

  &-laranja{
    background: $cor-laranja;
    color: white;
  }
}

.arial-bold {
  font-family: 'Arial', sans-serif;
  font-weight: 700;
}

.link-texto {
  font-family: Arial, sans-serif;
  font-size: 14px;
  text-decoration: underline;
  font-weight: 700;
  color: #646464;
  line-height: 16px;
  transition: all .3s ease;

  &:hover {
    color: $cor-laranja;
    text-decoration: underline;
  }

  &--footer{
    color: $cor-laranja;
    font-size:10px;
    line-height: 11px;
    font-weight: 400;
    text-decoration: none;

    &:hover {
      color: #C44C00;
    }
  }

  &--laranja {
    color: $cor-laranja;

    &:hover {
      color: #C44C00;
    }
  }

  &--roxo {
    color: $cor-roxo;

    &:hover {
      color: #3C04C1;
    }
  }
}

.float-sociais {
  padding: 15.5px 6.5px 15.5px 7.5px;
  background-color: #ffffff;
  position: fixed;
  bottom: 0;
  right: 0;
  border-radius: 6px 0 0 0;
  box-shadow: 0 3px 6px 0 rgba(0,0,0, 0.08);
  
  @include media-min768 {
    border-radius: 6px 0 0 6px;
    bottom: initial;
    top: 40%;
  }

  &--item {
    font: normal 8px/9px Arial, sans-serif;
    color: #008ed1;
    margin-bottom: 13px;
    display: flex;
    flex-direction: column;
    align-items: center;

    &:hover {
      color: darken($color: #008ed1, $amount: 10%);
    }

    &:last-of-type {
      margin-bottom: 0;
    }

    p { margin: 0; }

    i,
    span {
      font-size: 19px;
      margin-bottom: 3px;
    }
  }
}

.daterangepicker{
  .calendar-table {
    .prev,
    .next {
      span {
        border-color: $cor-roxo;
      }

    } 
  }

  td.active,
  td.active:hover {
    background-color: $cor-roxo !important;
  }

  td.off.end-date,
  td.off.in-range,
  td.off.start-date {
    color: white;
  }

  td.in-range {
    background-color: lighten($color: $cor-roxo, $amount: 40%);
  }
}

.sect {

  &-header {
    margin-top: 42px;
    margin-bottom: 28px;

    .titulo { margin: 0; }

    hr {
      border-color: #D3D3D3;
      margin: 15px 0 0;
    }
  }

  &-texto {
    margin: 0;
    font: normal 14px/16px Arial, sans-serif;
    color: #1E1E1E;

    & + .sect-texto {
      line-height: 20px;
    }
  }

  &-titulo {
    display: flex;
    align-items: center;
  }

  &-footer {
    margin-bottom: 44px;
    display: flex;
    flex-direction: column;
    align-items: center;
    
    @include media-min768 {
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-start;
    }
  }

  &-btn {
    max-width: 269px;
    width: 100%;
    text-align: center;

    .btn-link {
      margin-top: 16px;
      height: auto;
      color: #646464;
      
      @include media-min768 { margin-top: 32px; }

      &:hover {  @extend .c-laranja; }
    }
  }
}

.steps {
  padding: 24px 40px;
  margin-bottom: 40px;
  overflow-x: auto;
  background-color: #FFFFFF;
  border-radius: 10px;

  &-lista {
    padding: 0;
    margin: 0;
    min-width: 980px;
    display: flex;

    @include media-min992 {
      justify-content: space-around;
    }
  }

  .step {
    margin-bottom: 0;

    & + .step{
      position: relative;
      margin-left: 40px;

      &::before{
        content: '';
        position: absolute;
        top: 28%;
        left: -70px;
        z-index: 1;
        width: 80px;
        height: 0;
        border-top: 3px dashed #C7C7C7;
      }
    }

    &-icon {
      position: relative;
      z-index: 2;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100px;
      height: 100px;
      background-color: #C7C7C7;
      border-radius: 50%;
      font-size: 47px;
      color: white;
    }

    &-conteudo {
      text-align: center;

      .step-data {
        display: inline-block;
        font-family: Arial, sans-serif;
        font-size: 12px;
        color: #363636;
        line-height: 14px;
        margin: 16px 0 5px;
      }

      .h6 {
        max-width: 100px;
        margin-left: auto;
        margin-right: auto;
      }
    }
  }

  .step.step--active {
    .step-icon {
      background-color: #FF8D0A;
    }

    .step-conteudo {
      .h6 {
        color: #FF8D0A;
      }
    }
  }
}

.pagination {
  
  .page-item {
    .icon {
      font-size: 8px;
    }

    .page-link {
      border-color: #C9C9C9;
      color: $cor-roxo;
    }
  }

  .page-item.active {
    .page-link {
      border-color: $cor-roxo;
      background-color: $cor-roxo;
      color: white;
    }
  }
}

.icone-excel{
  fill: #008ed1;
}

.fornecedores-height{
  min-height: calc(100vh - 492px);
}

.mais-produtos-titulo {
  margin-bottom: 30px;
}

.mais-produtos-cards {
  margin-bottom: 52px !important;

  .card-item {
    @include media-min992 {
      max-width: calc((100% - 50px) / 5);
    }
  }
}

.mais-produtos-4-cards {
  margin-bottom: 38px !important;

  .card-item {
    @include media-min992 {
      max-width: calc((100% - 40px) / 4);
    }
  }
}