/* ==========================================================================
   Forms
   ========================================================================== */

.form-control{
  height: 48px;
  color: black;
  font-size: 14px;
  border: 1px solid #C9C9C9;
  border-radius: 6px;

  &::placeholder{
    color: #C9C9C9;
  }
}

.input-search{
  position: relative;
  display: block;
  width: 100%;
  margin-bottom: 0;

  .form-control{
    padding-left: 24px;
    padding-right: 50px;
    font-size: 14px;
    border-color: #D4D4D4;
    border-radius: 40px;

    &::placeholder{
      color: black;
    }
  }

  .icon{
    position: absolute;
    top: 50%;
    right: 19px;
    font-size: 22px;
    color: $cor-laranja;
    transform: translateY(-50%);
  }

  &--fornecedores{

    .form-control{
      border-radius: 4px;
      padding-right: 15px;
      padding-left: 40px;
    }

    .icon{
      right: inherit;
      left: 16px;
      font-size: 14px;
      color: #B8B8B8;
    }

  }

  &.input-lg{

    .form-control{
      height: 50px;
    }
  }

}

.input-password{
  position: relative;
  width: 100%;

  .form-control{
    padding-right: 35px;
  }

  .btn{
    position: absolute;
    top: 50%;
    right: 10px;
    color: #C9C9C9;
    transform: translateY(-50%);
  }
}

.select2-container .select2-selection--multiple{
  min-height: 50px;
}

.select2-container--default .select2-selection--multiple .select2-selection__rendered .select2-search.select2-search--inline {
  height: 47px;
  line-height: 34px;
}

.select2-container--default .select2-selection--multiple{
  border-color: #C9C9C9;
  line-height: 12px;
  transition: border ease 0.6s;
}

.select2-container--default.select2-container--focus .select2-selection--multiple{
  border-color: darken(#C9C9C9, 5%);
}

.select2-container--default .select2-selection--multiple .select2-selection__choice{
  @extend .tag;
  @extend .tag--roxo;
  position: relative;
  padding-right: 33px !important;
  border: none;

  .select2-selection__choice__remove{
    position: absolute;
    right: 12px;
    margin-right: 0;
    color: white;
    font-size: 20px;
    vertical-align: top;
    transition: all ease 0.6s;
  }

  .select2-selection__choice__remove:hover{
    color: white;
    opacity: 0.5;
  }

}

.form {
  &-adicionar {
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    //width: calc(100% - 100px);

    @include media-min768 {
      margin: 0;
    }

    &--btn {
      padding: 0;
      font: bold 26px/21px Arial, sans-serif;
      color: #008ed1;

      &:hover {
        color: darken($color: #008ed1, $amount: 10%);
      }
    }
  }

  &-pedidos {
    margin-bottom: 40px;
    display: flex;
    flex-direction: column;

    @include media-min768 {
      flex-direction: row;
      align-items: flex-end;
    }

    &--wrapper {
      margin-bottom: 10px;

      @include media-min768 {
        margin-right: 8px;
        margin-bottom: 0;
      }
    }

    .input {
      width: 100%;

      @include media-min768 {
        width: 294px;
      }
    }

    .btn {
      @include media-min768 { max-width: 140px; }
    }
  }

  &-fale-conosco {
    fieldset {
      @include media-min992 {
        margin-bottom: 20px;
      }

      &:last-of-type { margin-bottom: 0; }
    }

    .input {
      margin-bottom: 10px;

      @include media-min992 {
        margin-bottom: 0;
      }
    }

  }
}

.overflow-auto .cards-produto .form-adicionar{
  width: calc(100% - 100px);
}

.checkbox {
  display: none;

  & + label {
    margin-bottom: 14px;
    display: flex;
    align-items: center;
    font-family: Arial, sans-serif;
    font-size: 14px;
    color: #828282;
    cursor: pointer;
    text-transform: uppercase;

    &::before {
      content: "\f00c";
      margin-right: 9px;
      color: transparent;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 16px;
      height: 16px;
      border: solid 1px #B7B7B7;
      border-radius: 3px;
      font-family: "Font Awesome 5 Free";
      font-size: 8px;
      font-weight: 900;
      -moz-osx-font-smoothing: grayscale;
      -webkit-font-smoothing: antialiased;
      font-style: normal;
      font-variant: normal;
      text-rendering: auto;
      line-height: 1;
      transition: all 0.4s ease;
    }
  }

  &:checked + label::before {
    background-color: #FF8D0A;
    border-color: #FF8D0A;
    color: white;
  }

  &:checked + label {
    color: #FF8D0A;
    font-weight: 700;
  }
}

.select {
  margin: 0;
  position: relative;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #C9C9C9;
  border-radius: 6px;

  &::after {
    content: '\f0d7';
    @extend .fas;
    position: absolute;
    top: 50%;
    right: 27px;
    transform: translateY(-50%);
    color: #7A7A7A;
  }

  &.selected {
    select {
      color: darken($color: #C9C9C9, $amount: 20%);
    }
  }

  select {
    padding: 18px 46px 14px 22px;
    width: 100%;
    background: none;
    font: normal 16px/18px Arial, sans-serif;
    border: none;
    color: #C9C9C9;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;

    &::-ms-expand {
      display: none;
    }
  }
}

.input {
  padding: 12px;
  width: 100%;
  display: flex;
  background-color: #FFFFFF;
  border: 1px solid #C9C9C9;
  border-radius: 6px;
  align-items: center;

  &-label {
    margin-bottom: 3px;
    font: 700 12px/14px Arial, sans-serif;
    color: #1E1E1E;
  }
}

.textarea {
  border-radius: 4px;
  min-height: 115px;
}

.label-daterangepicker {
  margin: 0 0 10px 0;
  padding: 13px;
  width: 100%;
  display: flex;
  height: 50px;
  font: normal 14px/16px Arial, sans-serif;
  background-color: #FFFFFF;
  border: 1px solid #C9C9C9;
  border-radius: 6px;
  align-items: center;
  color: black;

  @include media-min768 { width: 245px; margin-bottom: 0;  }

  .icon {
    margin-right: 12px;
    font-size: 22px;
    color: #B8B8B8;
  }
}

.input-daterangepicker {
  margin-left: 2px;
  width: 100%;
  background: none;
  border: none;
}

::-webkit-input-placeholder,
:-ms-input-placeholder,
::placeholder {
  color: #C9C9C9;
}