/* ==========================================================================
   Reset
   ========================================================================== */

html,
body,
main {
  min-height: 100%;
  scroll-behavior: smooth;
}

* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a{
  color: inherit;
  transition: all ease 0.4s;

  &:hover,
  &:active,
  &:focus,
  &:focus:hover { transition: all ease 0.4s; }

  &:hover { text-decoration: inherit; }
}

img, iframe, object, video {
  max-width: 100%;
  width: auto;
  height: auto;
}

form {
  &:after,
  &:before {
    display: table;
    content: " ";
  }
  &:after { clear: both; }
}

textarea {
  max-width: 100%;
  width: 100%;
}

b {
  font-weight: 700;
}
