.produtos {
    &-detalhe {
        margin-top: 0;
        margin-bottom: 0;
        padding-bottom: 50px;
        justify-content: flex-start;

        @include media-min1200 {
            padding-bottom: 20px;
        }

        .sect-footer {
            margin-bottom: 27px;
        }

        &--wrapper {
            width: 100%;
            
            @include media-min992 {
                width: calc(100% - 245px);
                margin-left: 37px;
            }
        }

        &--img {
            text-align: center;

            @include media-min768 { text-align: left; }
        }

        &--conteudo {
            padding: 30px 34px;
            margin-bottom: 40px;
            background-color: white;
            border-radius: 6px;

            @include media-min768 { display: flex; }
        }

        &--body {
            
            @include media-min768 {
                width: 70%;
                margin-left: 44px;
            }

            .titulo {
                margin-bottom: 5px;
            }

            .form-adicionar {
                justify-content: flex-start;

                .btn--roxo {
                    width: 100%;
                    margin-left: 15px;

                    @include media-min992{
                        max-width: 165px;
                    }
                }
            }
        }

        &--codigo {
            margin-bottom: 9px;
            font: normal 14px/16px Arial, sans-serif;
            color: #818181;
        }

        &--info {
            margin-bottom: 20px;
            display: flex;

            .misc {
                margin-right: 10px;
                display: flex;
                border: 1px solid #D5D5D5;

                p {
                    margin: 0;
                    padding: 8px;
                    font: normal 12px/14px Arial, sans-serif;
                    color: #1E1E1E;

                    &:first-of-type {
                        border-right: 1px solid #D5D5D5;
                    }

                    &:last-of-type {
                        font-weight: bold;
                    }
                }
            }
        }

        &--valor {
            margin-bottom: 5px;
            font: bold 34px/39px Arial, sans-serif;
            color: #1E1E1E;

            small{
                font-weight: normal;
                font-size: 12px;
                line-height: 14px;
            }
        }

        &--valor-total {
            margin-bottom: 20px;
            font-size: 12px;
            line-height: 14px;
            color: #1E1E1E;
        }

        &--estoque {
            font: normal 12px/14px Arial, sans-serif;
            color: #B5B5B5;
        }

        &--descricao {
            margin-bottom: 14px;

            p {
                margin: 0;
                font: normal 10px/11px Arial, sans-serif;
                color: #B5B5B5;

                &:first-of-type {
                    margin-bottom: 10px;
                    font-size: 12px;
                    line-height: 14px;
                    color: #000000;
                }
            }
        }
    }
}