.modal {
    .modal-content {
        border: none;
    }

    .modal-header {
        .close {
            color: #363636;
            font-size: 16px;
            opacity: 1;
            transition: all 0.4s ease;

            &:hover {
                opacity: 1;
                color: darken($color: #363636, $amount: 20%);
            }
        }
    }
}

.modal-boleto {

    .modal-content {
        border: none;
        border-radius: 10px;
        padding: 20px 40px 28px;
    }

    .modal-header {
        display: block;
        align-items: inherit;
        justify-content: inherit;
        padding: 0;
        border-bottom: none;
        border-top-left-radius: inherit;
        border-top-right-radius: inherit;

        &_container {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            padding-top: 20px;
            color: #1E1E1E;
            font-size: 16px;

            .titulo {
                max-width: 310px;
                margin-top: 18px;
                text-align: center;

                & + p{
                    margin-bottom: 25px;
                }
            }
        }
        
        .close {
            position: absolute;
            top: 18px;
            right: 18px;
            opacity: 1;
            transition: all .3s ease;
            
            .fas {
                color: #363636;
            }
        }
    }

    .modal-body {
        padding: 0 1rem;
        margin: 0 0 20px;

        .lista-modal {
            margin: 0;
            padding: 0 0 0 5px;

            .lista-modal_item {
                font-family: Arial, sans-serif;
                font-size: 16px;
                line-height: 32px;
                color: #1E1E1E;

                span {
                    font-weight: 700;
                }
            }
        }
    }

    .modal-footer {
        justify-content: center;
        border-top: none;
        border-bottom-left-radius: inherit;
        border-bottom-right-radius: inherit;

        .btn {
            //margin-bottom: 20px;
            min-width: 324px;
        }
    }
}

.modal-nota{
    .modal-content {
        padding: 20px 20px 28px;
    }

    .modal-footer .btn{
        min-width: 195px;
    }
}

.modal-checkout {

    .modal-header,
    .modal-footer {
        border: none;
    }

    .modal-header,
    .modal-body {
        text-align: center;
    }

    .modal-header {
        padding-bottom: 0;
    }

    .modal-body {
        padding-top: 0;

        .titulo {
            margin-bottom: 16px;
        }

        p {
            margin: 0;
            font: normal 16px/18px Arial, sans-serif;
            color: #1E1E1E;
        }
    }

    .modal-footer {
        justify-content: center;
        padding-bottom: 40px;

        .btn {
            width: 228px;
        }
    }
}