.minha-conta {
    min-height: calc(100vh - (160px + 145px));
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @include media-min992 { min-height: calc(100vh - (168px + 126px)); }
    
    &--conteudo {
        @include media-min768 { padding-left: 20px; }
    }


    .h4 + .sect-texto { margin-top: 10px; }

    .sect-texto + .h4 { margin-top: 46px; }

    .h4 + .h5 { margin-top: 20px; }

    .h5 + .sect-texto {
        margin-top: 7px;
        line-height: 20px;
    }
    
    .sect-footer {
        margin-top: 40px;
        margin-bottom: 22px;
    }
}