.conteudo_interno {
    display: flex;
    margin-top: 48px;

    @include media-min992 {
        margin-bottom: 68px;
    }

    .filtro-sticky {
        margin-right: 22px;
        position: sticky;
        top: 120px;
        min-width: 230px;
        height: 100%;

        .form-sticky {
            margin-top: 40px;
            margin-bottom: 30px;

            &:last-of-type {
                margin-top: 0;
            }

            .form-group {
                margin: 0;
                margin-bottom: 7px;
                max-height: 20px;
            }

            &_titulos {
                display: flex;
                justify-content: space-between;
                align-items: baseline;
                margin-bottom: 14px;

                .limpar-filtro {
                    display: inline-block;
                    font-family: Arial, sans-serif;
                    font-size: 10px;
                    font-weight: 700;
                    line-height: 11px;
                    color: #757171;
                    background: transparent;
                    border: none;
                }
            }
        }
    }

    .cards-container {
        overflow: auto;
        width: 100%;

        .overflow {
            overflow: auto;
        }

        .h4{
            margin-bottom: 0;
        }

        .cards {
            margin-bottom: 37px;
        }

        .card-item {
            width: calc(50% - 5px);

            &:nth-of-type(2n + 2){
                margin-right: 0;
            }

            @include media-min768 {
                &:nth-of-type(2n + 2){
                    margin-right: 10px;
                }
            }
        }

        .overflow-auto{
            margin-top: 14px;

            .card-item {
                width: 203px;

                &:nth-of-type(2n + 2){
                    margin-right: 10px;
                }

                @include media-min992 {
                    width: 48%;
                }
            }
        }

    }
}