body {
    background-color: #F2F3F7;
}

.footer {
    background-color: #FFFFFF;

    &--texto {
        margin: 0;
        font-size: 10px;
        line-height: 11px;
        color: #757171;
    }

    .contato-container {
        padding: 18px 7px 10px;

        .nav-footer--lista {
            margin-bottom: 7px;
            display: flex;

            &_item {
                font-family: 'Arial', sans-serif;
                font-weight: 700;
                font-size: 10px;
                line-height: 11px;
                color: #757171;
            }

            li + li {
                &::before {
                    content: '|';
                    margin-left: 5px;
                    margin-right: 5px;
                }
            }

            &_link {
                &:hover {
                    color: #FF8D0A;
                }
            }
        }
    }

    .copyright {
        position: relative;
        padding: 13px 0;

        &::before {
            content: '';
            position: absolute;
            width: 100%;
            border-top: 1px solid #E1E1E1;
            top: 0;
            left: 0;
        }

        &--container {
            padding: 0 7px;
        }

        &--desenv {
            display: flex;
            justify-content: space-between;
            flex-direction: column;

            @include media-min992 {
                flex-direction: row;
            }
        }

        .footer--texto {
            margin-bottom: 8px;
        } 
    }
}