.checkout {
    margin-top: 42px;
    margin-bottom: 80px;

    &--container {
        position: relative;
        margin: 30px auto 37px;
        background-color: #FFFFFF;
        padding: 30px 20px 30px;
        border-radius: 6px;

        @include media-min992 {
            padding: 54px 45px 45px;
        }
    }

    &--sucesso {
        padding: 10px;
        border-radius: 6px;
        position: absolute;
        top: 0;
        left: 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        background-color: white;
        z-index: 2;
        opacity: 0;
        animation: fade-in 0.8s ease forwards;

        .btn--roxo {
            margin-top: 20px;
            max-width: 300px;
        }

        .icobutton {
            position: relative;
            width: 155px;
            height: 155px;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-bottom: 20px;

            .icon {
                position: relative;
                z-index: 9;
                font-size: 40px;
                color: white;
            }

            div {
                margin: 0;
                
                div {
                    svg {
                        ellipse {
                            fill: #008ed1;
                        }
                    }
                }
            }
        }
    }

    &--label {
        font-size: 14px;
        font-weight: 700;
        line-height: 16px;
        color: #1E1E1E;
    }

    &--campo {
        margin-bottom: 41px;

        &:last-of-type {
            margin-bottom: 0;

            .titulo {
                position: relative;
            }

            .titulo::after {
                content: '';
                position: absolute;
                top: 50%;
                width: 60%;
                background-color: #EBEBEB;
                height: 1px;
                margin-left: 10px;

                @include media-min992 {
                    width: 88%;
                }
            }
        }

        .form-group {
            display: flex;
            flex-direction: column;
            margin: 0;
            margin-top: 20px;
            
            @include media-min992 {
                margin-left: 12px;
            }

            .select {
                max-width: 513px;
            }
        }
    }

    &--texto {
        margin: 0;
        font-family: Arial, sans-serif;
        font-size: 16px;
        line-height: 18px;
        color: #1E1E1E;
        margin-top: 20px;
        
        @include media-min992 {
            margin-left: 12px;
        }

        &-icon {
            margin-bottom: 33px;
            display: flex;
            align-items: center;

            img { margin-right: 5px; }
        }
    }

    &--container_valor {
        display: flex;
        flex-direction: column;
        margin-top: 41px;
        
        @include media-min992 {
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            margin-left: 12px;
        }

        .btn {
            min-width: 100%;
            font-weight: 700;

            @include media-min992 {
                min-width: 300px;
            }
        }
    }

    &--texto_valor {
        margin: 0;
        font-size: 22px;
        margin-bottom: 20px;
        line-height: 26px;
        color: #525252;

        @include media-min992 {
            margin-bottom: 0;
        }
    }

    &--valor {
        font-weight: 700;
        color: $cor-laranja;
    }

    &--max_width {
        max-width: 524px;
    }
}

@keyframes fade-in {
    100% {
        opacity: 1;
    }
}