.conteudo_padrao {

    .cards-container {

        .titulo-container {
            display: flex;
            justify-content: space-between;
            margin-bottom: 22px;
            padding-bottom: 16px;
            position: relative;

            &::before {
                content: '';
                position: absolute;
                left: 0;
                bottom: 0;
                width: 100%;
                border-bottom: 1px solid #D3D3D3;
            }

            .total-itens {
                font-family: Arial, sans-serif;
                font-size: 14px;
                line-height: 16px;
                color: #1E1E1E;

                &_numero {
                    font-size: 14px;
                    line-height: 18px;
                    font-weight: 700;
                }
            }
        }

        .cards {
            margin-bottom: 0;
            .card-item {
                &:last-of-type {
                    margin-right: 0;
                }
            }

            &:last-of-type {
                margin-bottom: 22px;
                
                @include media-min992 {
                    margin-bottom: 0;
                }
            }
        }
    }
}