.tabela {
    width: 100%;
    border-collapse: collapse;
    
    thead {
        th {
            font: normal 12px/14px Arial, sans-serif;
            color: #838383;
        }
    }
    
    &-overflow {
        overflow: auto;
        border-radius: 10px;
    }

    .btn--link{
        display: inline-block;
        vertical-align: middle;
    }

    &-items {
        &--wrapper {            
            margin-bottom: 32px;
            padding: 30px 30px 20px 0;
            background-color: #FFFFFF;
            border-radius: 6px;
            overflow: auto;

            @include media-min992 {
                padding-left: 20px;
            }
        }

        &--estoque {
            color: #B1B1B1;
            font: normal 10px/11px Arial, sans-serif;
        }

        &--delete {
            font-size: 24px;
            color: #008ed1;

            &:hover {
                color: darken($color: #008ed1, $amount: 10%);
            }
        }

        td,
        th {
            background-color: white;
            &:first-of-type {
                position: sticky;
                left: 0;
            }
        }


        th {

            &:first-of-type {
                text-align: center;
            }

            &:nth-of-type(2n - 1) {
                padding: 0 46px;
            }
        }

        tbody {            
            tr {
                border-bottom: 1px solid #F2F2F2;

                img{
                    max-height: 90px;
                }
                
                &:first-of-type {
                    td {
                        padding-top: 30px !important;
                    }
                }

                &:last-of-type {
                    border: none;
                }

                td {
                    padding: 16px 0;
                    font: normal 16px/18px Arial, sans-serif;
                    color: #000;
                    background-color: white;
    
                    &:first-of-type {
                        padding-left: 0 !important;
                        padding-right: 0 !important;
                        text-align: center;
                    }

                    &:last-of-type {
                        padding-right: 30px;

                        @include media-min992 {
                            padding-right: 0;
                        }
                    }

                    &:nth-of-type(2n - 1) {
                        padding: 16px 46px;
                    }
    
                    &:nth-of-type(2){
                        min-width: 170px;

                        p {
                            margin: 0;
    
                            &:nth-of-type(2){
                                margin-top: 4px;
                                font: normal 12px/14px Arial, sans-serif;
                                color: #818181;
                            }
                        }
                    }
                }

                .form-adicionar{
                    width: auto;
                }

                &.indisponivel{

                    p,
                    td{
                        color: #C8C8C8 !important;
                    }

                    .tabela-items--estoque{
                        text-transform: uppercase;
                        color: #E47676 !important;
                    }
                }
            }
        }
    }

    &-listrada {
        width: 940px;
        background-color: white;

        @include media-min992 { width: 100%; }


        td,
        th {
            &:first-of-type {
                padding-right: 25px;
                padding-left: 25px;
                text-align: center;
            }
        }

        thead {
            th {
                padding-top: 24px;
                background-color: white;
            }
        }

        tbody {
            tr{
                &:nth-of-type(even){
                    td {
                        background-color: #FAFAFA;
                    }
                }

                &:first-of-type {
                    td {
                        padding-top: 28px !important;
                    }
                }

                td {
                    background-color: white;
                    padding: 14px 0;
                    font: normal 12px/14px Arial, sans-serif;
                    color: black;

                    .icon {
                        color: black;
                        font-size: 22px;
                    }

                    &:last-of-type {
                        padding-right: 30px;

                        @include media-min992 {
                            padding-right: 0;
                        }
                    }
                }
            }
        }
    }
}