/*!
 * Tipografia
 * 
 * 
 */

@font-face {
    font-family: 'robotoregular';
    src: url('../fonts/roboto-regular-webfont.eot');
    src: url('../fonts/roboto-regular-webfont.eot?#iefix') format('embedded-opentype'),
         url('../fonts/roboto-regular-webfont.woff2') format('woff2'),
         url('../fonts/roboto-regular-webfont.woff') format('woff'),
         url('../fonts/roboto-regular-webfont.ttf') format('truetype'),
         url('../fonts/roboto-regular-webfont.svg#robotoregular') format('svg');
    font-weight: normal;
    font-style: normal;
}


@font-face {
    font-family: 'icon';
    src: url('../fonts/icon.eot');
    src: url('../fonts/icon.eott?#iefix') format('embedded-opentype'),
         url('../fonts/icon.woff') format('woff'),
         url('../fonts/icon.ttf')  format('truetype'),
         url('../fonts/icon.svg#icon') format('svg');
}

.icon {
    font-family: 'icon';
    font-size: inherit;
    font-weight: normal;
    font-style: normal;
    color: inherit;

    &-trash {
        &::before {
            content: '\e900';
        }
    }

    &-search {
        &::before {
            content: '\e901';
        }
    }

    &-box-open {
        &::before {
            content: '\e902';
        }
    }

    &-truck {
        &::before {
            content: '\e903';
        }
    }

    &-eye {
        &::before {
            content: '\e904';
        }
    }

    &-check-2 {
        &::before {
            content: '\e905';
        }
    }

    &-doc {
        &::before {
            content: '\e906';
        }
    }

    &-bar-code {
        &::before {
            content: '\e907';
        }
    }

    &-box {
        &::before {
            content: '\e908';
        }
    }

    &-brightness {
        &::before {
            content: '\e909';
        }
    }

    &-calendar {
        &::before {
            content: '\e90a';
        }
    }

    &-caret-down {
        &::before {
            content: '\e90b';
        }
    }

    &-check {
        &::before {
            content: '\e90c';
        }
    }

    &-times {
        &::before {
            content: '\e90d';
        }
    }

    &-doc-excel {
        &::before {
            content: '\e90e';
        }
    }

    &-eye-slash {
        &::before {
            content: '\e90f';
        }
    }

    &-facebook-rounded {
        &::before {
            content: '\e910';
        }
    }

    &-instagram {
        &::before {
            content: '\e911';
        }
    }

    &-invoice {
        &::before {
            content: '\e912';
        }
    }

    &-chevron-down {
        &::before {
            content: '\e913';
        }
    }

    &-chevron-left {
        display: inline-block;
        transform: rotate(90deg);

        &::before {
            content: '\e913';
        }
    }

    &-chevron-right {
        display: inline-block;
        transform: rotate(-90deg);

        &::before {
            content: '\e913';
        }
    }

    &-shopping-basket {
        &::before {
            content: '\e914';
        }
    }

    &-user {
        &::before {
            content: '\e915';
        }
    }
    
    &-shopping-cart {
        &::before {
            content: '\e916';
        }
    }
}