.cards {
    display: flex;
    flex-wrap: wrap;
    overflow-x: auto;
}

.card-item {
    display: flex;
    flex-direction: column;
    width: 48%;
    margin: 0 10px 10px 0;
    background-color: white;
    border-radius: 6px;

    &:nth-of-type(2n + 2){
        margin-right: 10px;

        @include media-min768 { margin-right: 10px; }
    }
    
    @include media-min768 {
        width: 100%;
        max-width: 204px;
    }

    &--header {
        position: relative;
        padding: 0 6px;
        display: flex;
        height: 127px;
        align-items: center;
        justify-content: center;
        overflow: hidden;
    }

    &--body {
        flex: 1;
        padding: 0 12px;
        margin-bottom: 8px;
        text-align: center;
    }

    &--footer {
        position: relative;
        display: flex;
        flex-direction: column;
        padding: 12px;
        border-top: 1px solid #F2F2F2;
        
        @include media-min768 {
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
        }
    }

    &--titulo {
        margin-bottom: 13px;
        font: bold 12px/14px Arial, sans-serif;
        color: black;
    }

    &--codigo {
        margin-bottom: 6px;
        font: normal 7px/7px Arial, sans-serif;
        color: #818181;
    }

    &--valor {
        margin: 0 0 8px 0;
        font: normal 23px/27px Arial, sans-serif;
        color: black;

        small{
            font-size: 8px;
            line-height: 9px;
        }
    }

    &--valor-total {
        margin: 0 0 15px 0;
        font-size: 11px;
        color: black;
    }

    &--estoque {
        margin: 0;
        font: normal 10px/14px 'Segoe UI', sans-serif;
        color: #BCBCBC;
    }

    &--counter {
        padding: 7px 3px;
        margin: 0 8px;
        max-width: 36px;
        text-align: center;
        border: 2px solid #DEDEDE;
        border-radius: 6px;
        color: #818181;
        font: bold 18px/21px Arial, sans-serif;
    }
}

.img-card-produto {
    max-width: 50%;
    max-height: 90%;
    
    @include media-min768 {
        max-width: 100%;
    }
}

.card-sociais {
    margin-bottom: 11px;
    border-radius: 4px;
    max-width: 295px;
    margin-right: 10px;
    background-color: white;

    @include media-min768{
        margin-right: 10px;
        max-width: 204px;
    }

    &--header {
        padding: 7px 8px;
        display: flex;
        justify-content: space-between;

        .btn--azul { font-size: 7px; }
    }

    &--body {
        padding: 0 8px 0 7px;
    }

    &--footer {
        display: flex;
        padding: 5px 11px 7px 9px;
        border-top: 1px solid #EBEBEB;
        align-items: center;
        justify-content: space-between;
    }

    &--perfil {
        color: #414141;
        display: flex;

        &_logo {
            margin-right: 3px;
            display: flex;
            width: 23px;
            height: 23px;
            border-radius: 50%;
            border: 1px solid #E5E5E5;
            align-items: center;
            justify-content: center;
        }

        &_nome {
            font: bold 10px/11px Arial, sans-serif;
            color: inherit;
        }

        &_descricao {
            font: normal 7px/7px Arial, sans-serif;
            color: inherit;
        }

        p {  margin: 0; }
    }

    &--img {
        height: 201px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
    }

    &--veja-mais {
        margin-bottom: 6px;
        border-bottom: 1px solid #EBEBEB;
        a {
            margin: 0;
            font: bold 8px/9px Arial, sans-serif;
            color: #3897F0;

            &:hover { color: darken($color: #3897F0, $amount: 10%); }
        }
    }

    &--curtidas {
        color: #202020;
        margin-bottom: 8px;

        i { font-size: 14px; }

        p { font: bold 8px/9px Arial, sans-serif; margin: 0; }
    }

    &--descricao {
        margin-bottom: 7px;
        font: normal 8px/9px Arial, sans-serif;
        color: black;

        a {
            color: #3897F0;

            &:hover { color: darken($color: #3897F0, $amount: 10%); }
        }
    }

    &--data {
        margin: 0;
        font: bold 8px/9px Arial, sans-serif;
        color: #BBBBBB;
    }

    .btn--icon {
        color: #818181;
        font-size: 14px;
    }
}