/* ==========================================================================
   Header
   ========================================================================== */

.header{
  position: relative;
  z-index: 30;
  background-color: #4e555b;
  transition: all ease 0.4s;

  &.fixed{
    position: fixed;
    top: 0;
    width: 100%;

    &.small{
      .header_topo{
        min-height: 88px;
      }

      .header_nav{
        height: 3px;
        overflow: hidden;

        .nav{
          opacity: 0;
        }
      }
    }

    &.open{

      .header_nav{
        height: 48px;
        overflow: visible;

        .nav{
          opacity: 1;
          transition: all ease 0.3s;
          transition-delay: 0.2s;
        }
      }
    }

  }

  &-logado {
    .dropdown_user_msg {
      width: 140px;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }

  &_topo{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    min-height: 160px;
    padding: 12px 24px;
    transition: all ease 0.4s;

    @include media-min992{
      min-height: 120px;
      padding: 0 15px;
      flex-wrap: nowrap;
    }
  }

  .logo{
    max-width: 100px;

    @include media-min992{
      max-width: inherit;
    }
  }

  &_search{
    width: calc(90% - 32px);

    @include media-min992{
      width: 100%;
      max-width: 500px;
    }
  }

  .dropdown_user{

    .dropdown-toggle{
      display: flex;
      text-align: left;
      font-size: 15px;
      line-height: 18px;
      color: $cor-laranja;

      small{
        display: block;
        font-size: 11px;
        color: #828282;
      }
      
      .icon-chevron-down {
        font-size: 9px;
      }

      &::after{
        content: none;
      }

      &:focus{
        box-shadow: none;
      }
    }

    &_img{
      display: block;
      align-self: center;
      width: 30px;
      height: 30px;
      font-size: 30px;
      line-height: 30px;
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      border-radius: 50%;
    }

    .esq-senha{
      margin-top: 12px;
      padding: 0;
      font-size: 12px;
      color: #9D9D9D;
      border: none;

      &:hover,
      &:focus{
        color: $cor-roxo;
      }

    }

    .dropdown-menu{
      margin-top: 3px;
      margin-right: -38px;
    }

    .btn-link{
      height: inherit;
    }

    &_msg{
      max-width: 145px;
      text-overflow: ellipsis;
      overflow: hidden;
    }
  }

  .dropdown_cart{

    .dropdown-menu{
      min-width: 330px;
      margin-top: 20px;
      margin-right: -30px;
    }

    .dropdown-toggle{
      display: flex;
      align-items: center;
      font-size: 27px;
      color: $cor-laranja;

      @include media-min992{
        font-size: 30px;
      }

      &::after{
        content: none;
      }

      &:focus{
        box-shadow: none;
      }
    }

    &_number {
      font-size: 16px;
      line-height: 18px;
      font-weight: bold;
    }

    &_item{

      .dropdown-menu_link{
        display: block;
        font-size: 14px;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
      }

      &_footer{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 8px;
        font-size: 12px;
        color: #5C5C5C;

        p{
          margin-bottom: 0;
        }
      }

      & + .dropdown_cart_item{
        margin-top: 15px;
        padding-top: 15px;
        border-top: 1px solid #EEEEEE;
      }
    }

    .dropdown-menu_cont{
      max-height: 285px;
      overflow: auto;

      &::-webkit-scrollbar {
        width: 6px;
      }

      &::-webkit-scrollbar-track {
        background: white;
      }

      &::-webkit-scrollbar-thumb {
        background: #AEAEAE;
        border-radius: 20px;
      }

      &::-webkit-scrollbar-thumb:hover {
        background: #555;
      }
    }
  }

  .btn-menu{
    width: 20px;
    display: block;

    @include media-min992{
      display: none;
    }

    span{
      display: block;
      width: 100%;
      height: 3px;
      background-color: $cor-laranja;
      border-radius: 8px;

      &:first-of-type{
        width: 75%;
      }

      &:last-of-type{
        width: 50%;
      }

      & + span{
        margin-left: 0;
        margin-top: 4px;
      }
    }

    &:focus{
      box-shadow: none;
    }

  }

}

.header_nav{
  position: fixed;
  left: -100%;
  top: 0;
  z-index: 31;
  display: flex;
  flex-direction: column;
  width: 80%;
  max-width: 375px;
  height: 100%;
  background-color: white;
  transition: left ease-out 0.6s;

  @include media-min992{
    position: static;
    width: 100%;
    max-width: 100%;
    height: 48px;
    display: block;
    background-color: $cor-roxo;
    transition: all ease 0.4s;
  }

  &.active{
    left: 0;
  }

  .btn-close{
    position: absolute;
    top: 18px;
    right: -10vw;
    z-index: 3;
    font-size: 24px;
    color: white;
    transform: translateX(50%);

    &:focus{
      box-shadow: none;
    }
  }

  &.header_nav_right{
    left: inherit;
    right: -100%;
    transition: right ease-out 0.6s;

    &.active{
      right: 0;
    }

    .btn-close{
      left: -10vw;
      transform: translateX(-50%);
    }
  }

  .btn-user{
    width: 100%;
    height: 48px;
    padding: 0 24px;
    border-radius: 0;
  }

  .titulo{
    margin-bottom: 24px;
    color: #606060;
  }

  .nav{
    flex: 1;
    background-color: transparent;
    color: #606060;
    overflow-y: auto;

    @include media-min992{
      color: white;
      overflow: visible;
    }

    ul{
      display: flex;
      flex-direction: column;
      width: 100%;

      @include media-min992{
        flex-direction: row;
        align-items: center;
      }
    }

    &-link{
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0;
      font-size: 16px;
      line-height: 18px;

      @include media-min992{
        padding: 16px;
        font-size: 14px;
        line-height: 16px;
      }

       
      .icon{
        font-size: 12px;

        @include media-min992{
          font-size: inherit;
        }
      }

      &:hover,
      &:focus,
      &.active{
        color: $cor-roxo;

        @include media-min992{
          background-color: #005D85;
          color: white;
        }
      }
    }

    &-item{
      position: relative;

      & + .nav-item{
        margin-top: 24px;

        @include media-min992{
          margin-top: 0;
        }
      }

      &--separado{
        padding-top: 24px;
        color: #A4A4A4;
        border-top: 1px solid #D4D4D4;
      }

    }

    .nav_sub-menu{
      display: none;

      @include media-min992{
        position: absolute;
        left: 0;
        z-index: 3;
        min-width: 250px;
        width: auto;
        padding: 10px 0;
        background-color: #005D85;
      }

      &_link{
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        padding: 12px 24px;
        font-size: 14px;
        white-space: nowrap;

        @include media-min992{
          padding-top: 6px;
          padding-bottom: 6px;
        }

        .fa,
        .fas,
        .far{
          font-size: 12px;
        }

        &:hover,
        &.active{
          color: $cor-roxo;

          @include media-min992{
            background-color: #005D85;
            color: white;
          }
        }
      }

      .nav_sub-menu--interno{
        z-index: 2;
        top: 0;
        left: 90%;
        height: 100%;
        background-color: white;
        color: #9E9E9E;
        opacity: 0;
        transition: all ease 0.4s;

        &.active{
          left: 100%;
          opacity: 1;
        }

        .nav_sub-menu_link{

          &:hover{
            background-color: transparent;
            color: $cor-laranja;
            text-decoration: underline;
          }

          &.nav-dropdown{

            &:hover{
              text-decoration: none;

              span:first-of-type{
                text-decoration: underline;
              }
            }

          }

          &.active{
            background-color: #f3f3f3;
            color: $cor-laranja;
          }
        }

        .nav_sub-menu--interno{
          background-color: #f3f3f3;
        }
      }

      &.nav_sub-menu--interno{
        flex-wrap: wrap;
        flex-direction: column;
        align-items: flex-start;

        &.active{
          display: flex !important;
        }
      }

      .todas-categorias{
        padding-top: 28px;

        &::before{
          content: '';
          display: block;
          position: relative;
          top: -15px;
          width: 100%;
          height: 1px;
          background-color: #008ed1;
        }

        .nav_sub-menu--interno{
          min-width: 400px;
        }

      }

    }

    &-dropdown{

      .icon{
        font-size: 4px;
        margin-left: 8px;
      }
    }
  }

  &_breadcrumb{
    margin-left: -24px;
    margin-right: -24px;
    margin-bottom: 24px;
    padding: 16px 24px;
    font-weight: 700;
    font-size: 14px;
    color: #606060;
    border-bottom: 1px solid #D4D4D4;

    span + span{
      margin-left: 16px;
    }

    &_link{

      &:hover,
      &:focus{
        color: $cor-roxo;
      }
    }
  }

  &_holder{
    position: relative;
    height: 100%;
    overflow: hidden;
  }

  &_cont{
    position: absolute;
    z-index: 1;
    display: flex;
    flex-direction: column;
    flex: 1;
    width: 100%;
    height: 100%;
    padding: 24px;
    background: white;
    transition: transform ease 0.6s;

    &.sub-menu{
      padding-top: 0;
    }

    &.center{
      z-index: 2;
      transform: translateX(0);
    }

    &.left{
      transform: translateX(-100%);
    }

    &.right{
      transform: translateX(100%);
    }

    &_footer{
      display: flex;
      flex-direction: column;
      flex: 1;
      justify-content: flex-end;
      padding-top: 24px;
    }

  }

  .esq-senha{
    margin-top: 20px;
    height: auto;
    font-size: 12px;
    color: #9D9D9D;
    text-decoration: none;
  }
}

.cart_list{
  overflow: auto;

  &_link{
    display: block;
    font-size: 14px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }

  &_item{

    &_footer{
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: 8px;
      font-size: 12px;
      color: #5C5C5C;

      p{
        margin-bottom: 0;
      }
    }

    & + .cart_list_item{
      margin-top: 15px;
      padding-top: 15px;
      border-top: 1px solid #EEEEEE;
    }
  }
}