.breadcrumb {
    padding: 0;
    margin-bottom: 24px;
    display: flex;
    background: none;

    li {
        display: flex;
        font: normal 12px/14px Arial, sans-serif;
        color: #363636;

        a {
            text-decoration: underline;
            color: inherit;

            &:hover {
                color: darken($color: #363636, $amount: 10%);
            }
        }

        &:not(.active) {
            &::after {
                content: '>';
                margin: 0 9px;
                display: block;
            }
        }
    }

}