/* ==========================================================================
   Botões
   ========================================================================== */

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 6px 17px;
  border-radius: 6px;
  font-family: Arial, sans-serif;
  cursor: pointer;
  transition: all 0.4s ease;

  i + span,
  img + span,
  span + span,
  svg + span{
    margin-left: 16px;
  }

  svg{
    transition: inherit;
  }
}

// TAMANHOS

.btn {
  height: 40px;
  font-size: 14px;
  line-height: 26px;

  &--xs {
    height: 18px;
    font-size: 7px;
    line-height: 4px;
    border-radius: 3px;
  }

  &--sm {
    height: 34px;
    font-size: 12px;
    line-height: 22px;
  }

  &--lg {
    height: 50px;
    font-size: 16px;
    line-height: 38px;
    border-radius: 10px;
  }
}


// CORES

.btn {

  &--azul {
    background-color: #3897F0;
    color: #FFFFFF !important;

    &:hover {
      background-color: darken($color: #3897F0, $amount: 10%);
    }
  }

  &--cinza {
    background-color: #ADADAD;
    color: #FFFFFF !important;

    &:hover {
      background-color: darken($color: #ADADAD, $amount: 10%);
    }
  }

  &--cinza-claro {
    background-color: #F2F3F7;
    color: black !important;

    &:hover {
      background-color: darken($color: #F2F3F7, $amount: 10%);
    }
  }

  &--roxo {
    background-color: $cor-roxo;
    color: #FFFFFF !important;

    &:hover {
      background-color: darken($color: $cor-roxo, $amount: 10%);
    }
  }

  &--roxo-escuro {
    background-color: darken($cor-roxo, 20%);
    color: #FFFFFF !important;

    &:hover {
      background-color: darken($color: $cor-roxo, $amount: 30%);
    }
  }

  &--laranja {
    background-color: $cor-laranja;
    color: #FFFFFF !important;

    &:hover {
      background-color: darken($color: $cor-laranja, $amount: 10%);
    }
  }

  &--branco {
    background-color: #FFFFFF;
    color: $cor-roxo !important;

    &:hover {
      background-color: darken($color: $cor-roxo, $amount: 10%);
      color: #FFFFFF !important;

      svg{
        fill: white !important;
      }
    }
  }
}

// UTILIDADE

.btn {
  &--icon {
    padding: 0;
    height: auto;
    background: transparent;
    border: none;
  }

  &--bold {
    font-weight: bold;
  }

  &--full {
    width: 100%;
  }

  &--full-height {
    height: 100%;
  }

  &--block {
    display: block;
  }

  &-link {
    justify-content: flex-start;
    padding: 0;
    text-decoration: underline;

    &:hover {
      @extend .c-roxo;
    }
  }
}

.btn-user{
  display: flex;
  align-items: center;
  border: none;

  &_img{
    display: block;
    width: 25px;
    height: 25px;
    font-size: 25px;
    border-radius: 50%;
  }

  &_msg{
    font-size: 16px;
    line-height: 18px;
  }

  span + span{
    margin-left: 8px;
  }

}

.btn-add{
  position: absolute;
  right: 12px;
  width: 91px;
  padding-left: 0;
  padding-right: 0;

  &.active{
    width: calc(100% - 24px);
    background-color: #55CA38;
  }

  &:disabled{
    opacity: 1;
  }
}