.pedidos {

    &-titulo { margin-bottom: 13px; }

    &-filtro {
        .btn--cinza {
            margin-top: 107px !important;
        }
    }

    .titulo {
        &.h6 { font-weight: 400; }
    }
    
    .arial-bold {
        font-size: 16px;
        line-height: 18px;
    }

    .btn--cinza {
        margin-top: 42px;
        width: 92px;
    }

    &-detalhe {
        .tabela {
            
            &-overflow {
                margin-top: 20px;
                margin-bottom: 30px;

                @include media-min768 {
                    overflow: hidden;
                }
            }

            th,
            td {
                
                i {
                    font-size: 20px;
                }
            }

            tbody {
                tr {
                    td {
                        font-size: 14px;
                        line-height: 16px;
                    }
                }
            }
        }

        &--wrapper {
            margin-bottom: 14px;
            @include media-min992 { margin-bottom: 24px; }
        }


        &--item {
            margin-bottom: 16px;

            &-titulo {
                margin-bottom: 8px;
                font: normal 12px/14px Arial, sans-serif;
                color: #838383;
            }

            &-conteudo {
                margin: 0;
                font: normal 16px/18px Arial, sans-serif;
                color: #1E1E1E;

                &.bold {
                    font-weight: bold;
                }
            }
        }

        .btn--branco {
            display: flex;
            align-items: center;
            justify-content: center;

            @include media-min992 { font-size: 13px; }
            @include media-min1200 { font-size: inherit; }

            i,
            .icon {
                font-size: 20px;
            }
        }
    }
}